/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    font-family: 'Roboto', sans-serif;
    line-height: 1.6;
    background: #004d40; /* Lighter green background */
    color: #f0f0f0; /* Light text color */
    display: flex;
    flex-direction: column;
    margin: 0;
}

/* Root container */
#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    flex-grow: 1; /* Ensures content fills available space */
}

/* Global Styles */
h1, h2, h3 {
    margin: 20px 0;
    color: #f0f0f0;
}

h1 {
    font-size: 3em;
    text-align: center;
    animation: fadeIn 2s ease-in-out;
}

h2 {
    font-size: 2.5em;
    text-align: center;
    margin-bottom: 20px;
}

h3 {
    font-size: 2em;
}

a {
    color: #80deea;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    flex: 1;
}

/* Hero Section */
.hero {
    background: #004d40; /* Lighter green background */
    color: white;
    padding: 60px 20px;
    text-align: center;
    position: relative;
}

.hero .profile-img {
    width: 200px; /* Bigger circle format */
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    animation: rotate 5s linear infinite;
}

.hero .profile-img:hover {
    transform: scale(1.1); /* Slight zoom effect on hover */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.hero .view-cv {
    font-size: 1.5em;
    font-weight: bold;
    margin-top: 20px;
    text-transform: uppercase;
    padding: 10px 20px;
    border: 2px solid #80deea;
    border-radius: 30px;
    transition: background-color 0.3s ease, color 0.3s ease;
    display: inline-block; /* Ensure the button is centered */
}

.hero .view-cv:hover {
    background-color: #80deea;
    color: #004d40;
}

/* About Section */
.about-container {
    display: flex;
    flex-direction: column;
    padding: 40px;
    max-width: 1200px;
    margin: 0 auto;
}

.about-me {
    background: #004d40; /* Lighter green background */
    border-radius: 10px;
    padding: 40px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;
    text-align: justify;
}

.about-me p {
    font-size: 1.2em;
    line-height: 1.8;
}

/* About Me Subsection Titles */
.about-me h3 {
    font-size: 1.5em;
    margin-bottom: 10px;
}

/* Education Section */
.education {
    padding: 40px;
    background: #004d40; /* Lighter green background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;
    display: flex;
    justify-content: space-between;
}

.education-left,
.education-right {
    flex: 1;
    margin: 10px;
}

.education-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.education-item .education-icon {
    font-size: 3em;
    color: #80deea;
    margin-right: 10px;
}

.education-item .education-details {
    flex-grow: 1;
}

.education-item h3 {
    font-size: 1.5em;
}

.education-item p {
    margin: 5px 0;
}

/* Hobbies Section */
.hobbies {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 40px;
    background: #004d40; /* Lighter green background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.hobby-item {
    display: flex;
    align-items: center;
    margin: 10px;
    text-align: center;
    flex-basis: 22%;
}

.hobby-item .hobby-icon {
    font-size: 3em;
    color: #80deea;
    margin-bottom: 10px;
}

.hobby-item p {
    font-size: 1.2em;
    color: #f0f0f0;
}

/* Research Interests Section */
.research-interests {
    padding: 40px;
    background: #004d40; /* Lighter green background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;
    text-align: justify;
}

.research-interests-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    margin-top: 20px;
}

.research-interest-card {
    background: #004d40; /* Lighter green background */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: left;
}

.research-interest-card h3 {
    font-size: 1.5em;
    margin-bottom: 10px;
}

.research-interest-card p {
    font-size: 1.1em;
    color: #f0f0f0;
}

/* Skills Section */
.skills {
    padding: 40px;
    background: #004d40; /* Lighter green background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;
    text-align: center;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    margin-top: 20px;
}

.skill-card {
    background: #004d40; /* Lighter green background */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.skill-card .icon {
    font-size: 3em;
    color: #80deea;
    margin-bottom: 10px;
}

.skill-card p {
    font-size: 1.2em;
    color: #f0f0f0;
}

/* Navbar */
.navbar {
    display: flex;
    justify-content: center;
    background: #333;
    padding: 15px 0;
}

.navbar a {
    color: white;
    margin: 0 15px;
    font-size: 1.2em;
}

.navbar a:hover {
    text-decoration: underline;
}

/* Footer */
.footer {
    background: #333;
    color: white;
    text-align: center;
    padding: 10px 0;
    width: 100%;
    flex-shrink: 0; /* Ensures footer sticks to bottom */
    margin-top: auto; /* Push footer to bottom if content is short */
}

/* Projects Section */
.projects-container {
    padding: 20px;
    background: #004d40; /* Lighter green background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 1200px;
    margin: 20px auto;
}

.projects-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.project-card {
    background: #004d40; /* Lighter green background */
    border: 1px solid #ddd;
    border-radius: 10px;
    margin: 10px;
    padding: 20px;
    width: 30%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.project-card img {
    width: 100%;
    height: auto;
    border-radius: 5px;
    margin-bottom: 10px;
}

.project-card a {
    color: #80deea;
    text-decoration: none;
}

.project-card a:hover {
    text-decoration: underline;
}

/* Certifications Section */
.certifications {
    padding: 20px;
    background: #004d40; /* Lighter green background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 1200px;
    margin: 20px auto;
}

.certifications-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.certification {
    background: #004d40; /* Lighter green background */
    border: 1px solid #ddd;
    border-radius: 10px;
    margin: 10px;
    padding: 20px;
    width: 30%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.certification img {
    width: 100px;
    height: auto;
    margin-bottom: 10px;
}

/* Contact Section */
.contact-container {
    background: #004d40; /* Lighter green background */
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    margin: 20px auto;
}

.contact-form {
    width: 100%;
    background: #004d40; /* Lighter green background */
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
}

.contact-form input::placeholder,
.contact-form textarea::placeholder {
    color: #aaa;
}

.contact-form button {
    padding: 10px 20px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.contact-form button:hover {
    background: #0056b3;
}

/* Education and Hobbies Layout Fix */
.education-item, .hobby-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/* Media Queries */
@media screen and (max-width: 768px) {
    .hero {
        padding: 40px 20px;
    }

    .education-item,
    .hobby-item,
    .research-interest-card,
    .skill-card {
        flex-direction: column;
        align-items: center;
    }

    .projects-grid, .certifications-grid {
        flex-direction: column;
        align-items: center;
    }

    .project-card, .certification {
        width: 80%;
    }
}

/* Additional Styles */
.profile-photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
}

.cv-link {
    display: block;
    text-align: center;
    margin: 0 auto;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    width: fit-content;
}

.cv-link:hover {
    background-color: #0056b3;
}
